body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.a11y {
  display: none;
}

.clickable {
  cursor: pointer;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.board {
  display: flex;
  flex-direction: column; }

.board__row {
  display: flex; }

.board-spacer {
  flex: 1 1; }

.cell {
  flex: 2 1;
  position: relative; }

.cell:after {
  content: "";
  display: block;
  padding-bottom: 100%; }

.cell .contents {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0.5em;
  padding: 0.5em;
  border: 2px solid goldenrod;
  border-radius: 4px;
  box-shadow: 0 3px 9px black, inset 0 0 9px white; }

.champion {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.champion {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.bench {
  border: 2px solid black;
  padding: 0.5em;
  display: flex; }

.bench-slot {
  border: 2px solid black;
  border-radius: 5px;
  flex: 1 1;
  height: 5em;
  margin: 0.1em; }
  .bench-slot.selected {
    border-color: goldenrod; }

.champion {
  background-color: black;
  color: white;
  height: 100%;
  text-align: center; }
  .champion .champion-tier {
    font-size: 1.5em; }

.draft .above-bar, .draft .refresh-panel, .draft .xp-panel {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.draft .draft-lock .lock {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; }

.draft .draft-lock .lock, .draft .draft-gold-bar .draft-gold {
  border-style: solid;
  border-width: 3px;
  border-radius: 3px;
  -webkit-border-image: linear-gradient(-45deg, #205257, #103435) 1 25%;
          border-image: linear-gradient(-45deg, #205257, #103435) 1 25%; }

.draft .draft-area {
  border-style: solid;
  border-width: 6px;
  border-radius: 3px;
  -webkit-border-image: -webkit-gradient(linear, left top, left bottom, from(#725C31), to(#42361D)) 1 25%;
  -webkit-border-image: linear-gradient(to bottom, #725C31, #42361D) 1 25%;
          border-image: -webkit-gradient(linear, left top, left bottom, from(#725C31), to(#42361D)) 1 25%;
          border-image: linear-gradient(to bottom, #725C31, #42361D) 1 25%; }

.draft .above-bar {
  border-style: solid;
  border-width: 6px;
  border-radius: 3px;
  -webkit-border-image: -webkit-gradient(linear, left top, left bottom, from(#816C34), to(#725C31)) 1 25%;
  -webkit-border-image: linear-gradient(to bottom, #816C34, #725C31) 1 25%;
          border-image: -webkit-gradient(linear, left top, left bottom, from(#816C34), to(#725C31)) 1 25%;
          border-image: linear-gradient(to bottom, #816C34, #725C31) 1 25%; }

.draft {
  padding: 0.5em;
  display: flex;
  margin-top: 3em; }
  .draft .draft-area {
    border-radius: 3px;
    position: relative;
    background-color: black; }
  .draft .above-bar {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    border-bottom: none;
    border-radius: 3px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background-color: black;
    color: white; }
  .draft .draft-sidebar {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    border-right: none;
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px; }
  .draft .draft-sidebar-items {
    display: flex;
    flex-direction: column; }
  .draft .sidebar-item {
    flex: 1 1;
    border: 2px solid goldenrod;
    margin: 0.5em;
    padding: 0.1em;
    background-color: black; }
    .draft .sidebar-item.refresh {
      border-color: #857347; }
    .draft .sidebar-item.xp {
      border-color: #3A7393; }
    .draft .sidebar-item .content {
      flex: 1 1; }
    .draft .sidebar-item .icon {
      width: 4em;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat; }
  .draft .draft-lock {
    padding: 0.5em;
    padding-bottom: 0; }
    .draft .draft-lock .lock {
      height: 2em;
      width: 4em;
      background-image: url("/img/lock-open.png"); }
      .draft .draft-lock .lock.closed {
        background-image: url("/img/lock-closed.png"); }
  .draft .draft-main {
    flex: 4 1;
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .draft .draft-gold-bar {
    padding: 0.5em;
    padding-bottom: 0; }
    .draft .draft-gold-bar .draft-gold {
      padding: 0.5em 1em; }
  .draft .hand {
    display: flex;
    z-index: 1;
    height: 100%; }
  .draft .refresh-panel {
    color: white;
    background-color: #857347;
    padding: 0.5em;
    display: flex; }
  .draft .xp-panel {
    display: flex;
    flex-direction: column;
    position: relative;
    background: #3A7393;
    color: white;
    padding: 0.5em; }
    .draft .xp-panel .details {
      display: flex; }
    .draft .xp-panel .level {
      position: absolute;
      bottom: 0;
      right: 0;
      height: 2em;
      width: 2em;
      padding: 0.5em;
      background-color: rgba(255, 255, 255, 0.5);
      vertical-align: middle;
      text-align: center; }
    .draft .xp-panel .xp-progress {
      width: 100%;
      height: 1em;
      flex: 1 1;
      display: flex; }
      .draft .xp-panel .xp-progress .xp-progress-bar {
        flex: 1 1;
        display: flex;
        flex-direction: column-reverse;
        padding: 0 2em 0 0.5em; }
        .draft .xp-panel .xp-progress .xp-progress-bar .bar {
          background-color: black;
          height: 1em;
          position: relative;
          overflow: hidden; }
        .draft .xp-panel .xp-progress .xp-progress-bar .fill {
          background-color: lightblue;
          height: 100%;
          width: 100%;
          -webkit-transition: -webkit-transform 0.2s;
          transition: -webkit-transform 0.2s;
          transition: transform 0.2s;
          transition: transform 0.2s, -webkit-transform 0.2s; }

.champ-card .hero, .champ-card .banner {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.champ-card .hero {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.champ-card {
  border: 3px solid black;
  border-radius: 3px;
  flex: 1 1;
  min-height: 8em;
  margin: 1em;
  display: flex;
  flex-direction: column;
  -webkit-filter: drop-shadow(-5px -5px 5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(-5px -5px 5px rgba(0, 0, 0, 0.5));
  position: relative; }
  .champ-card.champ-cost-1 {
    border-color: lightslategrey; }
    .champ-card.champ-cost-1 .decoration {
      background-image: none; }
  .champ-card.champ-cost-2 {
    border-color: green; }
    .champ-card.champ-cost-2 .decoration {
      background-image: url(/img/profile-deco/2.png); }
  .champ-card.champ-cost-3 {
    border-color: navy; }
    .champ-card.champ-cost-3 .decoration {
      background-image: url(/img/profile-deco/3.png); }
  .champ-card.champ-cost-4 {
    border-color: magenta; }
    .champ-card.champ-cost-4 .decoration {
      background-image: url(/img/profile-deco/4.png); }
  .champ-card.champ-cost-5 {
    border-color: gold; }
    .champ-card.champ-cost-5 .decoration {
      background-image: url(/img/profile-deco/5.png); }
  .champ-card .decoration {
    position: absolute;
    top: 0%;
    width: 100%;
    height: 25%;
    z-index: 100;
    background-repeat: no-repeat;
    background-position: top center; }
  .champ-card .hero {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: left;
    background-color: black;
    color: white;
    padding: 0.5em; }
  .champ-card .banner .cost img {
    display: inline-block;
    height: 1em;
    vertical-align: middle;
    margin-right: 0.1em; }

.champ-card .banner {
  display: flex;
  padding: 0.5em;
  color: white; }

.champ-card .banner .name {
  flex: 1 1;
  text-align: left; }

.champ-card .banner .cost {
  justify-self: flex-end;
  text-align: end;
  width: 2em; }

.banner-cost-1 {
  background-color: lightslategrey; }

.banner-cost-2 {
  background-color: green; }

.banner-cost-3 {
  background-color: navy; }

.banner-cost-4 {
  background-color: magenta; }

.banner-cost-5 {
  background-color: gold; }

.coin {
  display: inline-block;
  height: 1em;
  width: 1em;
  margin-right: 0.1em;
  background-image: url("/img/coin.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain; }

